import React from "react";
import Masonry from "react-masonry-css";
import Image from "react-bootstrap/Image";
import Moment from "moment";
import { saveAs } from "file-saver";
import { IconContext } from "react-icons";
import { FiDownload } from "react-icons/fi";

import Url from "../api/url";

const breakpoints = {
  default: 10,
  1900: 9,
  1800: 8,
  1600: 7,
  1400: 6,
  1100: 5,
  900: 4,
  700: 3,
  600: 2,
};

function ImageList({ imageList, handleOpenImage }) {

  const saveFile = (img, name) => {
    saveAs(img, name);
  };

  const readableBytes = (bytes) => {
    var i = Math.floor(Math.log(bytes) / Math.log(1024)),
      sizes = ["B", "KB", "MB", "GB", "TB", "PB", "EB", "ZB", "YB"];
    return (bytes / Math.pow(1024, i)).toFixed(2) * 1 + " " + sizes[i];
  };

  return (
    <Masonry
    breakpointCols={breakpoints}
      className="my-masonry-grid"
      columnClassName="my-masonry-grid_column"
      >
      {imageList.map((img, index) => (
        <div key={index} className="containerimage">
          <Image
            className="my-masonry-img"
            // src={img.image}
            src={img.thumbnail}
            fluid
            width={"100%"}
            onClick={() => handleOpenImage(img.id)}
            />
          <div className="topmetadata">
            <div className="d-flex justify-content-between">
              <div>
                {img.width.toLocaleString()} x {img.height.toLocaleString()}
              </div>
              <div>{readableBytes(img.size)}</div>
            </div>
          </div>

          <div className="metadata">
            <div className="d-flex justify-content-between">
              <div>
                {img.country} - {Moment(img.date).year()}
              </div>
              <div>
                <div
                  onClick={() => saveFile(img.image, img.id)}
                  style={{ cursor: "pointer" }}
                  >
                  <IconContext.Provider value={{ color: "#333", size: "18px" }}>
                    <FiDownload />
                  </IconContext.Provider>
                </div>
              </div>
            </div>
          </div>
        </div>
      ))}
    </Masonry>
  );
}

export default ImageList;
