import React, { useState, useEffect } from "react";
import axios from "axios";
import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Button from "react-bootstrap/Button";
import Select from "react-select";
import ReactPaginate from "react-paginate";
import Modal from "react-bootstrap/Modal";

import Url from "../api/url";
import ImageList from "../components/ImageList";
import Topbar from "../components/Topbar";
import Login from "../pages/Login";
import Logout from "../pages/Logout";
import Search from "../components/Search";
import ModalImgDetail from "../components/ModalImgDetail";
import DataForm from "../components/DataForm";
import { geturl } from "../api/getfirebase";

function Images(props) {
  const [loginas, setLoginas] = useState("");
  const [isLoading, setIsLoading] = useState(true);
  const [imageList, setImageList] = useState([]);
  const [imageCount, setImageCount] = useState(1);
  const [searchStr, setSearchStr] = useState("");
  const [resetPaginator, setResetPaginator] = useState(new Date().getTime());
  const [showLogin, setShowLogin] = useState(true);
  const [showLogout, setShowLogout] = useState(false);
  const [showModalImageDetail, setShowModalImageDetail] = useState(false);
  const [imgDetail, setImgDetail] = useState({});
  const [editMode, setEditMode] = useState(false);
  const [showModalForm, setShowModalForm] = useState(false);
  const [resetAddImageShow, setResetAddImageShow] = useState(
    new Date().getTime()
  );

  const headers = {
    Authorization: `Token ${localStorage.getItem("token")}`,
  };

  useEffect(() => {
    get_authuser();
    get_images(0);
    countItems();
  }, [resetAddImageShow, resetPaginator]);

  const get_authuser = async () => {
    axios
      // .get(Url.baseUrl + "/api/authentication/user/", {
      .get((await geturl()) + "/api/authentication/user/", {
        headers: headers,
      })
      .then((res) => {
        console.log('user: ', res.data);
        setLoginas(res.data.username);
      })
      .catch((err) => {
        console.log(err);
      });
  };

  const get_images = async (myoffset) => {
    setIsLoading(true)
    axios
      .get(
        (await geturl()) +
          `/products/?search=${searchStr}&limit=${itemsPerPage}&offset=${myoffset}&ordering=-uploaded_at`,
        {
          headers: headers,
        }
      )
      .then((res) => {
        setImageList(res.data.results);
        setIsLoading(false)
        // console.log("xxx: " + res.data.results[0]);
      })
      .catch((err) => {
        console.log(err);
      });
  };

  const countItems = async () => {
    axios
      .get((await geturl()) + "/products/", {
        headers: headers,
      })
      .then((res) => {
        setImageCount(res.data.count);
      })
      .catch((err) => {
        console.log(err);
      });
  };

  //Paginator
  const itemsPerPage = 50;
  const [currentItems, setCurrentItems] = useState(null);
  const [pageCount, setPageCount] = useState(0);
  const [offset, setOffset] = useState(0);
  const [itemOffset, setItemOffset] = useState(0);

  useEffect(() => {
    // Fetch items from another resources.
    const endOffset = itemOffset + itemsPerPage;
    // console.log(`Loading items from ${itemOffset} to ${endOffset}`);
    // setCurrentItems(items.slice(itemOffset, endOffset));
    setPageCount(Math.ceil(imageCount / itemsPerPage));
  }, [itemOffset, itemsPerPage, imageCount]);

  // Invoke when user click to request another page.
  const handlePageClick = (event) => {
    const newOffset = (event.selected * itemsPerPage) % imageCount;
    setOffset(newOffset);
    // console.log(
    //   `User requested page number ${event.selected}, which is offset ${newOffset}`
    // );
    setItemOffset(newOffset);
    get_images(newOffset);
  };

  const handleOpenImage = (id) => {
    const res = imageList.filter(function (item) {
      return item.id == id;
    });
    const myimg = {};
    myimg["id"] = res[0].id;
    myimg["title"] = res[0].title;
    myimg["width"] = res[0].width;
    myimg["height"] = res[0].height;
    myimg["size"] = res[0].size;
    myimg["orientation"] = res[0].orientation;
    myimg["author"] = res[0].author;
    myimg["country"] = res[0].country;
    myimg["date"] = res[0].date;
    myimg["uploaded_at"] = res[0].uploaded_at;
    myimg["user"] = res[0].user;
    myimg["image"] = res[0].image;
    myimg["loginas"] = loginas;
    
    // console.log(id);
    // console.log(myimg['user']);
    // console.log(loginas);
    setEditMode(false);
    setImgDetail(myimg);
    setShowModalImageDetail(true);
  };

  const handleCloseModal = () => setShowLogout(false);

  const updateSearchList = (res) => {
    setImageList(res);
  };

  const handleEditImage = () => {
    setEditMode(true);
    setShowModalImageDetail(false);
    setShowModalForm(true);
  };

  const handleCloseMID = () => setShowModalImageDetail(false);

  const showForm = () => {
    setShowModalForm(true);
    setEditMode(false);
  };
  const handleClose = () => setShowModalForm(false);

  const addImagetoList = async (pin) => {
    pin["image"] = (await geturl()) + pin["image"];
    setImageList([...imageList, pin]);
    get_images(0);
  };

  const updateImagetoList = () => {
    get_images(0);
  };

  return (
    <Container fluid>
      <div key={resetAddImageShow}>
        <Topbar
          setShowLogout={setShowLogout}
          showLogin={showLogin}
          loginas={loginas}
          showForm={showForm}
        />
      </div>
      {showLogin && (
        <Login
          setShowLogin={setShowLogin}
          setLoginas={setLoginas}
          setResetAddImageShow={setResetAddImageShow}
        />
      )}
      {showLogout && (
        <Modal show={showLogout} onHide={handleCloseModal}>
          <Logout setShowLogin={setShowLogin} setShowLogout={setShowLogout} />
        </Modal>
      )}
      {!showLogin && (
        <>
          <Row>
            <Col>
              <Search
                updateSearchList={updateSearchList}
                get_images={get_images}
                setSearchStr={setSearchStr}
                setImageCount={setImageCount}
                countItems={countItems}
                setResetPaginator={setResetPaginator}
              />
            </Col>
          </Row>
          <Row style={{ marginTop: 5 }}>
            <Col className="d-flex justify-content-end">
              <h4>Total: {imageCount.toLocaleString()}</h4>
            </Col>
          </Row>

          {isLoading ? <div className="spinner_cont"><div className="lds-ripple"><div></div><div></div></div></div> : 
            <Row>
              <div key={resetPaginator}>
                <ImageList
                  imageList={imageList}
                  handleOpenImage={handleOpenImage}
                  />
              </div>
            </Row>
          }
          <Row style={{ marginTop: 10 }}>
            <Col className="d-flex justify-content-center">
              <div key={resetPaginator}>
                <ReactPaginate
                  nextLabel="next >"
                  onPageChange={handlePageClick}
                  pageRangeDisplayed={5}
                  marginPagesDisplayed={2}
                  pageCount={pageCount}
                  previousLabel="< previous"
                  pageClassName="page-item"
                  pageLinkClassName="page-link"
                  previousClassName="page-item"
                  previousLinkClassName="page-link"
                  nextClassName="page-item"
                  nextLinkClassName="page-link"
                  breakLabel="..."
                  breakClassName="page-item"
                  breakLinkClassName="page-link"
                  containerClassName="pagination"
                  activeClassName="active"
                  renderOnZeroPageCount={null}
                  />
              </div>
            </Col>
          </Row>
        </>
      )}
      {showModalImageDetail && (
        <ModalImgDetail
          showModalImageDetail={showModalImageDetail}
          handleCloseMID={handleCloseMID}
          imgDetail={imgDetail}
          handleEditImage={handleEditImage}
        />
      )}
      {showModalForm && (
        <DataForm
          showModalForm={showModalForm}
          handleClose={handleClose}
          addImagetoList={addImagetoList}
          updateImagetoList={updateImagetoList}
          editMode={editMode}
          imgDetail={imgDetail}
        />
      )}
    </Container>
  );
}

export default Images;
