import React from "react";
import Modal from "react-bootstrap/Modal";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Image from "react-bootstrap/Image";
import Button from "react-bootstrap/Button";
import Card from "react-bootstrap/Card";
import Moment from "moment";
import { saveAs } from "file-saver";

import Url from "../api/url";

const getOrientationName = (orientation) => {
  if (orientation === 1) {
    return "Horizontal";
  } else if (orientation === 2) {
    return "Vertical";
  } else {
    return "Square";
  }
};

const readableBytes = (bytes) => {
  var i = Math.floor(Math.log(bytes) / Math.log(1024)),
    sizes = ["B", "KB", "MB", "GB", "TB", "PB", "EB", "ZB", "YB"];
  return (bytes / Math.pow(1024, i)).toFixed(2) * 1 + " " + sizes[i];
};

function ModalImgDetail({
  showModalImageDetail,
  handleCloseMID,
  imgDetail,
  handleEditImage,
}) {
  const saveFile = (img, name) => {
    saveAs(img, name);
  };

  function DetailForm() {
    return (
      <div className="detailbox">
        <div>
          <h5>
            {imgDetail["country"]} - {Moment(imgDetail["date"]).year()}
          </h5>
          <hr />
          <div>
            <b>Description:</b> {imgDetail["title"]}
          </div>
          <div>
            <b>Date:</b> {Moment(imgDetail["date"]).format("DD-MM-YYYY")}
          </div>
          <div>
            <b>Author:</b> {imgDetail["author"]}
          </div>
        </div>
        <div>
          <h5>Techinical Data</h5>
          <hr />
          <div>
            <b>Dimensions:</b> {imgDetail["width"].toLocaleString()} x{" "}
            {imgDetail["height"].toLocaleString()} px
          </div>
          <div>
            <b>Orientation:</b> {getOrientationName(imgDetail["orientation"])}
          </div>
          <div>
            <b>Size:</b> {readableBytes(imgDetail["size"])}{" "}
          </div>
          <div>
            <b>Uploaded date:</b>{" "}
            {Moment(imgDetail["uploaded_at"]).format("DD-MM-YYYY")}{" "}
          </div>
          <div>
            <b>Uploaded by:</b> {imgDetail["user"]}
          </div>
        </div>
      </div>
    );
  }

  return (
    <Modal
      show={showModalImageDetail}
      size="xl"
      onHide={handleCloseMID}
      style={{ maxHeight: "100%" }}
    >
      <Modal.Header closeButton>
        <Modal.Title>
          {((localStorage.getItem("editor") > 0 && imgDetail['user']==imgDetail['loginas']) || imgDetail['loginas']=='admin') && (
            <Button variant="primary" onClick={handleEditImage}>
              Edit
            </Button>
          )}

          <Button
            variant="secondary"
            onClick={() => saveFile(imgDetail["image"], imgDetail["id"])}
            style={{ marginLeft: 10 }}
          >
            Download
          </Button>
        </Modal.Title>
      </Modal.Header>
      <Modal.Body>
        {imgDetail["orientation"] === 1 ? (
          <Row className="imgcontainerhor">
            <Col sm={9}>
              <div>
                <Image className="imgdetailhor" src={imgDetail["image"]} />
              </div>
            </Col>
            <Col sm={3}>
              <DetailForm />
            </Col>
          </Row>
        ) : (
          <Row className="imgcontainerver">
            <Col sm={9}>
              <div className="imgcontainerver">
                <Image className="imgdetailver" src={imgDetail["image"]} />
              </div>
            </Col>
            <Col sm={3}>
              <DetailForm />
            </Col>
          </Row>
        )}
      </Modal.Body>
    </Modal>
  );
}

export default ModalImgDetail;
