import React, { useState, useEffect, useMemo, Fragment } from "react";
import axios from "axios";
import Modal from "react-bootstrap/Modal";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Form from "react-bootstrap/Form";
import Image from "react-bootstrap/Image";
import Button from "react-bootstrap/Button";
import Select from "react-select";
import DatePicker from "react-date-picker";
import Moment from "moment";

import Countries from "../api/countries";
import Url from "../api/url";

function DataForm({
  showModalForm,
  handleClose,
  addImagetoList,
  updateImagetoList,
  editMode,
  imgDetail,
}) {
  const [title, setTitle] = useState("");
  const [fileName, setFileName] = useState(null);
  const [country, setCountry] = useState("");
  const [author, setAuthor] = useState("");
  const [date, setDate] = useState(new Date());
  const [showModalDelete, setShowModalDelete] = useState(false);

  // const selCountryOptions = useMemo(() => countryList().getData(), []);

  const headers = {
    Authorization: `Token ${localStorage.getItem("token")}`,
  };

  useEffect(() => {
    if (editMode) {
      setTitle(imgDetail["title"]);
      setFileName(imgDetail["image"]);
      setCountry(imgDetail["country"]);
      setAuthor(imgDetail["author"]);
      const formatedDate = new Date(imgDetail["date"]);
      setDate(formatedDate);
    }
  }, []);

  var FormData = require("form-data");
  //   var fs = require("fs");
  var data = new FormData();
  data.append("title", title);
  data.append("image", fileName);
  data.append("country", country);
  data.append("author", author);
  data.append("date", Moment(date).format("YYYY-MM-DD"));

  var config = {
    method: "post",
    url: Url.baseUrl + "/productsapi/?",
    headers: {
      Authorization: `Token ${localStorage.getItem("token")}`,
      // "Access-Control-Allow": "*",
      // "content-type": "multipart/form-data",
      // Accept: "application/json",
    },
    data: data,
  };

  const addImage = () => {
    axios(config)
      .then(function (res) {
        // console.log(JSON.stringify(res.data));
        addImagetoList(res.data);
      })
      .catch(function (error) {
        console.log(error);
      });
    handleClose();
  };

  const updateImage = () => {
    console.log(country);

    const mydata = {
      title: title,
      author: author,
      country: country,
      date: Moment(date).format("YYYY-MM-DD"),
    };
    axios
      .put(Url.baseUrl + `/productsapi/${imgDetail["id"]}/`, mydata, {
        headers: headers,
      })
      .then((res) => {
        updateImagetoList();
      })
      .catch((err) => {
        console.log(err);
      });
    handleClose();
  };

  const handleCountryChange = (e) => {
    setCountry(e.value);
    console.log(e.value);
  };

  const handleCloseDeleteModal = () => setShowModalDelete(false);

  const handleDeleteImageConfirm = () => {
    axios
      .delete(Url.baseUrl + `/productsapi/${imgDetail["id"]}/`, {
        headers: headers,
      })
      .then((res) => {
        console.log(res);
        updateImagetoList();
      })
      .catch((err) => {
        console.log(err);
      });
    handleClose();
    setShowModalDelete(false);
  };

  return (
    <Fragment>
      <Modal show={showModalForm} size="xl" onHide={handleClose}>
        <Modal.Header closeButton>
          {editMode ? (
            <Fragment>
              <Button variant="primary" onClick={updateImage}>
                Update
              </Button>
              <Button
                style={{ marginLeft: 10 }}
                variant="danger"
                onClick={() => {
                  setShowModalDelete(true);
                }}
              >
                Delete
              </Button>
            </Fragment>
          ) : (
            <Button variant="primary" onClick={addImage}>
              Save
            </Button>
          )}
        </Modal.Header>
        <Modal.Body>
          <Form style={{ backgroundColor: "#a0c0a0", padding: 20 }}>
            <Row>
              <Col sm={5}>
                <Form.Group className="mb-3" controlId="f1">
                  <Form.Label>Description</Form.Label>
                  <Form.Control
                    name="title"
                    type="text"
                    value={title}
                    required
                    placeholder="Description"
                    onChange={(e) => setTitle(e.target.value)}
                  />
                </Form.Group>
                <Form.Label>Country</Form.Label>
                <Select
                  options={Countries.countries}
                  onChange={handleCountryChange}
                  value={Countries.countries.filter(function (option) {
                    return option.value === country;
                  })}
                />
                <Form.Group className="mb-3" controlId="f1">
                  <Form.Label>Date</Form.Label>
                  <br />
                  <DatePicker
                    onChange={setDate}
                    value={date}
                    format="dd-MM-y"
                  />
                </Form.Group>
                <Form.Group className="mb-3" controlId="f1">
                  <Form.Label>Author</Form.Label>
                  <Form.Control
                    name="author"
                    type="text"
                    value={author}
                    required
                    placeholder="Author"
                    onChange={(e) => setAuthor(e.target.value)}
                  />
                </Form.Group>
              </Col>
              <Col>
                {editMode ? (
                  <>
                    {imgDetail["orientation"] === 1 ? (
                      <div className="imgcontainerhor">
                        <Image className="imgdetailhor" src={fileName} />
                      </div>
                    ) : (
                      <div className="imgcontainerver">
                        <Image className="imgdetailver" src={fileName} />
                      </div>
                    )}
                  </>
                ) : (
                  <Form.Group controlId="formFile" className="mb-3">
                    <Form.Label>Default file input example</Form.Label>
                    <Form.Control
                      type="file"
                      label={fileName}
                      accept="image/*"
                      multiple={false}
                      onChange={(e) => setFileName(e.target.files[0])}
                    />
                  </Form.Group>
                )}
              </Col>
            </Row>
          </Form>
        </Modal.Body>
      </Modal>

      <Modal show={showModalDelete} onHide={handleCloseDeleteModal}>
        <Modal.Body>Are you sure you want to delete this image?</Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={handleCloseDeleteModal}>
            Close
          </Button>
          <Button variant="danger" onClick={handleDeleteImageConfirm}>
            Yes
          </Button>
        </Modal.Footer>
      </Modal>
    </Fragment>
  );
}

export default DataForm;
